import { Box, Paper, makeStyles, Typography } from "@material-ui/core";
import Grid from '@material-ui/core/Grid';
import { useTranslation } from "react-i18next";


const useStyles = makeStyles( theme => ({
  root: {
    width: "100%",
    textAlign: "center",
    height: "25vh",
    minHeight: "250px",
    backgroundColor: theme.palette.background.second,
  },
  col: {
    paddingTop: "2vh",
    maxWidth: "250px",
  },
  title: {
    fontSize: "20px",
  },
  content: {
    paddingTop: "1vh",
    fontSize: "10px",
    textAlign: "left",
  },
  items: {
    paddingBottom: "1vh",
    fontSize: "10px",
  },
  gridItem: {
    padding: 40,
    paddingTop: 30,
    paddingBottom: 0,
  }
}));


export default function Footer() {
  const classes = useStyles()
  const { t } = useTranslation()
  const contactItems = t("contact-items").split("|")
  const contactValues = t("contact-values").split("|")
  return (
    <Paper className={classes.root}>
      <Box className={classes.textBox}>
        <Grid container justify="center">
          <Grid item className={classes.gridItem}>
            <Box className={classes.col}>
              <Typography className={classes.title}>{t("footer-title1")}</Typography>
              <Typography className={classes.content}>{t("footer-introduce")}</Typography>
            </Box>
          </Grid>
          <Grid item className={classes.gridItem}>
            <Box className={classes.col}>
              <Typography className={classes.title}>{t("footer-title2")}</Typography>
              <div className={classes.content}>
                {t("scopes").split("|").map((s) => (
                  <Typography className={classes.items}>{s}</Typography>
                ))}
              </div>
            </Box>
          </Grid>
          <Grid item className={classes.gridItem}>
            <Box className={classes.col}>
              <Typography className={classes.title}>{t("footer-title3")}</Typography>
              <div className={classes.content}>
                {contactItems.map((s, i) => (
                  <Typography className={classes.items}>{s+": "+contactValues[i]}</Typography>
                ))}
              </div>
            </Box>
          </Grid>
        </Grid>
      </Box>
    </Paper>
  )
}
