import React from 'react';
import Page from '../base'
import MainContext from '../../components/MainContexts'
import Header from '../../components/Header'
import TreeView from '@material-ui/lab/TreeView';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TreeItem from '@material-ui/lab/TreeItem';
import { Grid } from '@material-ui/core';
import { Box } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import { useTranslation } from "react-i18next";

import MediaCard from '../../components/Card'
import SubTitle from '../../components/SubTitle'


const useStyles = makeStyles( theme => ({
  tree: {
    height: 240,
    flexGrow: 1,
    maxWidth: 400,
  },
  treePanel: {
    backgroundColor: theme.palette.background.second,
    borderRadius: "8px",
    padding: "20px",
    minHeight: "20vh",
  },
  cardsPanel: {
    backgroundColor: theme.palette.background.second,
    borderRadius: "8px",
    padding: "30px",
    minWidth: "60%",
    minHeight: "30vh",
  },
}));


export default function TechProd() {
  const { t } = useTranslation()
  const classes = useStyles()

  const cardStyle = {
    width: 230,
  }
  const imgStyle = {
    height: 180,
  }

  const fundItems = ["DNA-FISH", "RNA-FISH", "SSIP-Seq"]
  const clinItems = ["CancerDia", "PrenatalDia", "PhysExam"]

  const fundUrls = ["/techprod/dnafish", "/techprod/rnafish", "/techprod/ssipseq"]
  const clinUrls = ["/techprod/cancerdia", "/techprod/prenataldia", "/techprod/physexam"]

  return (
    <Page>
      <Header title={t("Tech&Prod")}></Header>
      <Box style={{padding: "2vh"}}></Box>
      <MainContext>
        <Grid container spacing={3}>

          <Grid item xs="3">
            <Box style={{padding: "4.4vh"}}></Box>
            <Box className={classes.treePanel}>
              <TreeView
                className={classes.root}
                defaultExpanded={['0', '1', '2']}
                defaultCollapseIcon={<ExpandMoreIcon />}
                defaultExpandIcon={<ChevronRightIcon />}
              >
                <TreeItem nodeId="0" label={t("Tech&Prod")}>
                  <TreeItem nodeId="1" label={t("FundSci")}>
                    {fundItems.map((s, i) => (<TreeItem nodeId={3+i+""} label={s} />))}
                  </TreeItem>
                  <TreeItem nodeId="2" label={t("ClinicalDia")}>
                    {clinItems.map((s, i) => (<TreeItem nodeId={6+i+""} label={t(s)} />))}
                  </TreeItem>
                </TreeItem>
              </TreeView>
            </Box>
          </Grid>

          <Grid item xs>
            <SubTitle text={t("FundSci")}></SubTitle>
            <Box className={classes.cardsPanel}>
              <Grid container spacing={2}>
                {
                  fundItems.map((s, i) => (
                    <Grid item xs>
                      <MediaCard cardStyle={cardStyle} mediaStyle={imgStyle}
                        image={process.env.PUBLIC_URL+"/imgs/techprod-card-"+s+".png"}
                        title={s} content=""
                        url={fundUrls[i]}
                        />
                    </Grid>
                  ))
                }
              </Grid>
            </Box>
            <Box style={{padding: "4vh"}}></Box>
            <SubTitle text={t("ClinicalDia")}></SubTitle>
            <Box className={classes.cardsPanel}>
              <Grid container spacing={2}>
                {
                  clinItems.map((s, i) => (
                    <Grid item xs>
                      <MediaCard cardStyle={cardStyle} mediaStyle={imgStyle}
                        image={process.env.PUBLIC_URL+"/imgs/techprod-card-"+s+".png"}
                        title={t(s)} content=""
                        url={clinUrls[i]}
                        />
                    </Grid>
                  ))
                }
              </Grid>
            </Box>
          </Grid>
        </Grid>
      </MainContext>
      <Box style={{padding: "4.4vh"}}></Box>
    </Page>
  )
}
