import React from 'react';
import './i18n'
import { ThemeProvider } from "@material-ui/core";
import { createMuiTheme } from '@material-ui/core/styles';
import {
  HashRouter as Router,
  Switch,
  Route
} from "react-router-dom";

import ScrollToTop from './components/ScrollToTop'
import Home from './pages/Home'
import About from './pages/About'
import Contact from './pages/Contact';
import TechProd from './pages/TechProd/Index';
import DNAFISH from './pages/TechProd/DNAFISH';
import RNAFISH from './pages/TechProd/RNAFISH';
import SSIPSeq from './pages/TechProd/SSIPSeq';
import CancerDia from './pages/TechProd/CancerDia';
import PrenatalDia from './pages/TechProd/PrenatalDia';
import PhysExam from './pages/TechProd/PhysExam';


const theme = createMuiTheme({
  palette: {
    type: 'dark',
    background: {
      default: "black",
      second: "#202020"
    }
  },
  MuiTypography: {
    variantMapping: {
      h1: 'h2',
      h2: 'h2',
      h3: 'h2',
      h4: 'h2',
      h5: 'h2',
      h6: 'h2',
      subtitle: 'h2',
      body1: 'span',
      body2: 'span',
    },
  },
});


function App() {
  return (
    <div className="App">
      <ThemeProvider theme={theme}>
        <Router>
          <Switch>
            <Route exact path="/">
              <ScrollToTop />
              <Home/>
            </Route>
            <Route path="/about">
              <ScrollToTop />
              <About/>
            </Route>
            <Route path="/techprod/dnafish">
              <ScrollToTop />
              <DNAFISH/>
            </Route>
            <Route path="/techprod/rnafish">
              <ScrollToTop />
              <RNAFISH/>
            </Route>
            <Route path="/techprod/ssipseq">
              <ScrollToTop />
              <SSIPSeq/>
            </Route>
            <Route path="/techprod/cancerdia">
              <ScrollToTop />
              <CancerDia/>
            </Route>
            <Route path="/techprod/prenataldia">
              <ScrollToTop />
              <PrenatalDia/>
            </Route>
            <Route path="/techprod/physexam">
              <ScrollToTop />
              <PhysExam/>
            </Route>
            <Route path="/techprod">
              <TechProd/>
            </Route>
            <Route path="/contact">
              <ScrollToTop />
              <Contact/>
            </Route>
          </Switch>
        </Router>
      </ThemeProvider>
    </div>
  );
}


export default App;
